@import 'defs';



pre, code{
  overflow-x: auto;
}

pre {
  font-family: $monospace;
  code:not([class]) {
    padding: bs(1);
    overflow-x: scroll;
  }
}


code, kbd {
  background: $code-bg;
  border-radius: .2 * bs(1);
  color: $code-color;
  // display: inline-block;
  padding: 0 bs(0.2); 
  font-family: $monospace;
  font-size: 90%;
}

kbd {
  color: $kbd-color;
  background: $code-bg;
}

mark {
  background: $primary;
  color: $site-bg;
  padding: 0 bs(0.2);
}
